import React from "react";
import "../../scss/TemperatureChart.scss";
import { Line } from "react-chartjs-2";

class AirHumidityChart extends React.Component {
  state = {
    api: "http://185.204.216.143:5000/api/wilg_powietrza_proc",
    temp: [],
  };
  componentDidMount() {
    fetch(this.state.api)
      .then((response) => {
        if (response.ok) {
          return response;
        }
        throw Error(response.status);
      })
      .then((response) => response.json())
      .then((temp) => {
        this.setState({
          temp,
        });
      })
      .catch((error) => console.log(error));
  }
  render() {
    let temperatures = this.state.temp.map((element) => {
      return element.wilg_powietrza_proc;
    });
    let labels = this.state.temp.map((element, index) => {
      return index + 1;
    });

    const data = {
      labels: labels,
      datasets: [
        {
          label: "Wilgotność powietrza",
          data: temperatures,
          borderColor: "skyblue",
        },
      ],
    };
    return (
      <div className="chartWrapper">
        <span>Wilgotność powietrza [%]</span>
        <Line data={data} />
      </div>
    );
  }
}

export default AirHumidityChart;
