import React from "react";
import "../../scss/TemperatureChart.scss";
import { Line } from "react-chartjs-2";

class TemperatureChart extends React.Component {
  state = {
    api: "http://185.204.216.143:5000/api/temp_punkt_rosy",
    api2: "http://185.204.216.143:5000/api/wilg_lisc_proc",
    temp: [],
    wilg: [],
  };
  componentDidMount() {
    //pkt rosy
    fetch(this.state.api)
      .then((response) => {
        if (response.ok) {
          return response;
        }
        throw Error(response.status);
      })
      .then((response) => response.json())
      .then((temp) => {
        this.setState({
          temp,
        });
      })
      .catch((error) => console.log(error));
    //wilg lisc
    fetch(this.state.api2)
      .then((response) => {
        if (response.ok) {
          return response;
        }
        throw Error(response.status);
      })
      .then((response) => response.json())
      .then((wilg) => {
        this.setState({
          wilg,
        });
      })
      .catch((error) => console.log(error));
  }
  render() {
    let temperatures = this.state.temp.map((element) => {
      return element.temp_punkt_rosy;
    });
    let wilgs = this.state.wilg.map((element) => {
      return element.wilg_lisc_proc;
    });
    let labels = this.state.temp.map((element, index) => {
      return index + 1;
    });

    const data = {
      labels: labels,
      datasets: [
        {
          label: "Temperatura punktu rosy",
          data: temperatures,
          borderColor: "green",
        },
        {
          label: "Wilgotność liścia",
          data: wilgs,
          borderColor: "royalblue",
        },
      ],
    };
    return (
      <div className="chartWrapper">
        <span>Temperatura punktu rosy [°C] i wilgotność liścia [%]</span>
        <Line data={data} />
      </div>
    );
  }
}

export default TemperatureChart;
