import React from "react";
import LoggedUser from "./Dumb components/LoggedUser";
import Navigation from "./Navigation";
import TemperatureChart from "./charts/TepmeratureChart";
import WspolczynnikNChart from "./charts/WspolczynnikNChart";
import PktRosyWilgLisc from "./charts/PktRosyWilgLisc";
import AirHumidityChart from "./charts/AirHumidityChart";
import "../scss/Stats.scss";

const Stats = () => {
  return (
    <div className="statisticsWrapper">
      <LoggedUser />
      <Navigation />
      <h1>Statystyki</h1>
      <div className="info">
        <h2>
          <i className="fas fa-info-circle"></i>Obserwuj zmiany na bieżąco.
        </h2>
        <p>
          Program generuje wykresy na podstawie danych meteorologicznych z
          ostatnich trzech dni.
        </p>
        <p>Każda jednostka na osi x to 15 min.</p>
        <p>Pojedynczy dzień to 96 jednostek.</p>
      </div>
      <div className="charts">
        <TemperatureChart />
        <WspolczynnikNChart />
        <PktRosyWilgLisc />
        <AirHumidityChart />
      </div>
    </div>
  );
};

export default Stats;
