import React from "react";
import "../scss/App.scss";
import LoginPage from "./LoginPage";
import HomePage from "./HomePage";
import ShowData from "./ShowData";
import { Route, Switch, BrowserRouter as Router } from "react-router-dom";
import Szara_plesn from "./Dumb components/Szara_plesn";
import Maczniak_prawdziwy from "./Dumb components/Maczniak_prawdziwy";
import Maczniak_rzekomy from "./Dumb components/Maczniak_rzekomy";
import UserLogOut from "./Dumb components/UserLogOut";
import UserService from "./Dumb components/UserService";
import ConfirmationBio from "./Dumb components/ConfirmationBio";
import ConfirmationChem from "./Dumb components/ConfirmationChem";
import ConfirmationSulfur from "./Dumb components/ConfirmationSulfur";
import Stats from "./Stats";

//api

class App extends React.Component {
  state = {
    data: [],
    loggedIn: false,
    loggedUser: "",
    // api: "http://77.55.211.245:3000/api/data",
    api: "http://185.204.216.143:5000/api/data",
  };

  handleUserLogin = (user) => {
    this.setState({
      loggedIn: !this.state.loggedIn,
      loggedUser: user,
    });
  };

  componentDidMount() {
    fetch(this.state.api)
      .then((response) => {
        if (response.ok) {
          return response;
        }
        throw Error(response.status);
      })
      .then((response) => response.json())
      .then((data) => {
        this.setState({
          data,
        });
      })
      .catch((error) => console.log(error));
  }

  render() {
    const { loggedUser } = this.state;
    const indexRi = this.state.data.map((element) => {
      return element.wsp_ri;
    });
    if (this.state.loggedIn) {
      return (
        <Router>
          <Switch>
            <Route
              path="/"
              exact
              render={() => (
                <LoginPage
                  handleLogin={this.handleUserLogin}
                  loggedIn={this.state.loggedIn}
                />
              )}
            />
            <Route
              path="/home"
              render={() => (
                <HomePage data={this.state.data} user={loggedUser} />
              )}
            />
            <Route
              path="/data"
              render={() => <ShowData data={this.state.data} />}
            />
            <Route
              path="/szara_plesn"
              render={() => <Szara_plesn value={0} />}
            />
            <Route
              path="/confirmBio"
              render={() => <ConfirmationBio value={indexRi} />}
            />
            <Route
              path="/confirmChem"
              render={() => <ConfirmationChem value={indexRi} />}
            />
            <Route
              path="/confirmSulf"
              render={() => <ConfirmationSulfur value={indexRi} />}
            />
            <Route
              path="/maczniak_prawdziwy"
              render={() => <Maczniak_prawdziwy value={indexRi} />}
            />
            <Route
              path="/maczniak_rzekomy"
              render={() => <Maczniak_rzekomy value={0} />}
            />
            <Route
              path="/user_log_out"
              render={() => <UserLogOut handleLogin={this.handleUserLogin} />}
            />
            <Route
              path="/service"
              render={() => <UserService user={loggedUser} />}
            />
            <Route path="/stats" render={() => <Stats />} />
          </Switch>
        </Router>
      );
    } else
      return (
        <Router>
          <Route
            path="/"
            render={() => (
              <LoginPage
                handleLogin={this.handleUserLogin}
                loggedIn={this.state.loggedIn}
              />
            )}
          />
        </Router>
      );
  }
}
export default App;
