import React, { Component } from "react";
import "../scss/ShowData.scss";
import Navigation from "./Navigation";
import LoggedUser from "./Dumb components/LoggedUser";

class ShowData extends Component {
  render() {
    const data = this.props.data.map((element) => (
      <li key={element.id} className="singleValue">
        <div className="data">
          <p className="description">
            <i className="fas fa-caret-right"></i> Dane fizycznie zmierzone:
          </p>
          <p className="time">
            <span>Czas pomiaru:</span>{" "}
            <span>
              {element.czas_pomiaru.slice(0, 10)}, godz.
              {element.czas_pomiaru.slice(11, 16)}
            </span>
          </p>
          <p>
            <span>Temperatura powietrza:</span>
            <span> {element.temp_powietrza} °C</span>
          </p>
          <p>
            <span>Temperatura punktu rosy:</span>
            <span> {element.temp_punkt_rosy} °C</span>
          </p>
          <p>
            <span>Temperatura termometru mokrego:</span>
            <span>{element.temp_term_mokr} °C</span>
          </p>
          <p>
            <span>Wilgotność powietrza:</span>
            <span> {element.wilg_powietrza_proc} %</span>
          </p>
          <p>
            <span>Wilgotność liścia:</span>
            <span> {element.wilg_lisc_proc} %</span>
          </p>
          <p>
            <span>Prędkość wiatru:</span>
            <span> {element.predkosc_wiatru_m_s} m/s</span>
          </p>
          <p>
            <span>Kierunek wiatru:</span>
            <span> {element.kierunek_wiatru} </span>
          </p>
          <p>
            <span>Opad godzinowy:</span>
            <span> {element.opad_godzinowy_litr_m2} l/m²</span>
          </p>
          <p>
            <span>Opad dobowy:</span>
            <span> {element.opad_dobowy_litr_m2} l/m²</span>
          </p>
        </div>

        <div className="computed">
          <p className="description">
            <i className="fas fa-caret-right"></i> Dane wyliczone:
          </p>
          <p>
            <span>Współczynnik n:</span>
            <span> {element.wsp_n}</span>
          </p>
          <p>
            <span>Współczynnik Ri:</span>
            <span> {element.wsp_ri}</span>
          </p>
          <p>
            <span>Bargraf niski:</span>
            <span> {element.bargraf_niski}</span>
          </p>
          <p>
            <span>Bargraf średni:</span>
            <span> {element.bargraf_sredni}</span>
          </p>
          <p>
            <span>Bargraf wysoki:</span>
            <span> {element.bargraf_wysoki}</span>
          </p>
        </div>
        <div className="tech">
          <p className="description">
            <i className="fas fa-caret-right"></i> Dane techniczne:
          </p>
          <p>
            <span>Napięcie zasilania:</span>
            <span> {element.napiecie_zasil_V} V</span>
          </p>
          <p>
            <span>Prąd pobierany:</span>
            <span> {element.prad_pobierany_mA} mA</span>
          </p>
        </div>
      </li>
    ));
    return (
      <div className="dataWrapper">
        <Navigation />
        <LoggedUser />
        <h1>Aktualne dane</h1>
        <div className="dataFromBase">
          <ul>{data}</ul>
        </div>
      </div>
    );
  }
}

export default ShowData;
