import React from "react";
import { Link } from "react-router-dom";
import "../scss/HomePage.scss";
import LoggedUser from "./Dumb components/LoggedUser";
import Navigation from "./Navigation";
import RiskChart from "./Dumb components/RiskChart";

const HomePage = (props) => {
  const temp = props.data.map((element) => {
    return element.temp_powietrza;
  });
  const wilg = props.data.map((element) => {
    return element.wilg_powietrza_proc;
  });
  const opad = props.data.map((element) => {
    return element.opad_dobowy_litr_m2;
  });

  const wiatr = props.data.map((element) => {
    return element.predkosc_wiatru_m_s;
  });
  const wilgLisc = props.data.map((element) => {
    return element.wilg_lisc_proc;
  });
  const indexRi = props.data.map((element) => {
    return element.wsp_ri;
  });
  const zasil = props.data.map((element) => {
    return element.napiecie_zasil_V;
  });
  const date = new Date().toString().slice(0, 24);
  let now = new Date().getTime();

  let showDaysBio =
    Math.floor((localStorage.getItem("goalBio") - now) / 86400000) + 1;
  let showDaysChem =
    Math.floor((localStorage.getItem("goalChem") - now) / 86400000) + 1;
  let showDaysSulf =
    Math.floor((localStorage.getItem("goalSulf") - now) / 86400000) + 1;
  return (
    <div className="homePageWrapper">
      <Navigation />
      <LoggedUser />
      <h1>Stacja pogody</h1>
      <div className="starterData">
        <span className="hello">Dzień dobry.</span>
        <p className="hello">
          Zalogowałeś się jako:{" "}
          <span style={{ color: "orangered" }}>{props.user.name}</span>
        </p>
        <div className="infoElement">
          <span></span>
        </div>
        <div className="infoElement">
          <i className="far fa-calendar-alt"></i> <span>Data:</span>
          <span>{date}</span>
        </div>
        <div className="infoElement">
          <i className="fas fa-thermometer-three-quarters"></i>
          <span>Temperatura:</span>
          <span>{temp} °C</span>
        </div>
        <div className="infoElement">
          <i className="fas fa-tint"></i>
          <span>Wilgotność powietrza:</span>
          <span>{wilg} %</span>
        </div>
        <div className="infoElement">
          <i className="fas fa-cloud-showers-heavy"></i>
          <span>Opad dobowy:</span>
          <span>{opad} l/m²</span>
        </div>
        <div className="infoElement">
          <i className="fas fa-wind"></i>
          <span>Prędkość wiatru:</span>
          <span>{wiatr} m/s</span>
        </div>
        <div className="infoElement">
          <i className="fas fa-leaf"></i>
          <span>Wilgotność liścia:</span>
          <span>{wilgLisc} %</span>
        </div>
        <div className="infoElement">
          <i className="fas fa-car-battery"></i>
          <span>Napięcie zasilania:</span>
          <span style={zasil >= 10.5 ? { color: "black" } : { color: "red" }}>
            {zasil >= 10.5 ? `${zasil} V` : `Awaria! ${zasil} V`}
          </span>
        </div>
      </div>
      <div className="starterGraphs">
        <div className="singleElement">
          <p>Mączniak prawdziwy</p>
          <RiskChart value={indexRi} />
          <Link to="maczniak_prawdziwy">Dodaj oprysk</Link>
          {showDaysBio < 0 ? null : (
            <>
              <span className="daysNumber">
                Dnia <strong>{localStorage.getItem("clickdateBio")} </strong>
                wykonano oprysk{" "}
                <strong>{localStorage.getItem("nameBio")}</strong>.
              </span>
              <span className="daysNumber">
                Przed następnym opryskiem odczekaj{" "}
                <strong>{showDaysBio < 0 ? 0 : showDaysBio}</strong> dni.
              </span>
            </>
          )}

          {showDaysSulf < 0 ? null : (
            <>
              <span className="daysNumber">
                Dnia <strong>{localStorage.getItem("clickdateSulf")} </strong>
                wykonano oprysk{" "}
                <strong>{localStorage.getItem("nameSulf")}</strong>.
              </span>
              <span className="daysNumber">
                Przed następnym opryskiem odczekaj{" "}
                <strong>{showDaysSulf < 0 ? 0 : showDaysSulf}</strong> dni.
              </span>
            </>
          )}

          {showDaysChem < 0 ? null : (
            <>
              <span className="daysNumber">
                Dnia <strong>{localStorage.getItem("clickdateChem")} </strong>
                wykonano oprysk{" "}
                <strong>{localStorage.getItem("nameChem")}</strong>.
              </span>
              <span className="daysNumber">
                Przed następnym opryskiem odczekaj{" "}
                <strong>{showDaysChem < 0 ? 0 : showDaysChem}</strong> dni.
              </span>
            </>
          )}
        </div>
        <div className="singleElement">
          <p>Mączniak rzekomy</p>
          <RiskChart value={20} />
          <Link to="/maczniak_rzekomy">Dodaj oprysk</Link>
          <span className="daysNumber">
            Brak zainstalowanego modelu chorobowego.
          </span>
        </div>
        <div className="singleElement">
          <p>Szara Pleśń</p>
          <RiskChart value={45} />
          <Link to="/szara_plesn">Dodaj oprysk</Link>
          <span className="daysNumber">
            Brak zainstalowanego modelu chorobowego.
          </span>
        </div>
      </div>
    </div>
  );
};

export default HomePage;
