import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import "../scss/Navigation.scss";

const Navigation = () => {
  const nav = [
    {
      id: 1,
      path: "/home",
      name: "Start",
    },
    {
      id: 2,
      path: "/data",
      name: "Dane pomiarowe",
    },
    {
      id: 3,
      path: "/stats",
      name: "Statystyki",
    },
    {
      id: 4,
      path: "/maczniak_prawdziwy",
      name: "Mączniak Prawdziwy",
    },
    {
      id: 5,
      path: "/maczniak_rzekomy",
      name: "Mączniak Rzekomy",
    },
    {
      id: 6,
      path: "/szara_plesn",
      name: "Szara Pleśń",
    },
    {
      id: 7,
      path: "/service",
      name: "Serwis użytkownika",
    },
  ];
  const [status, setStatus] = useState("close");
  return (
    <div className="navigationWrapper">
      <div
        className={`burgerContainer ${status}`}
        role="button"
        onClick={() => setStatus(status === "open" ? "close" : "open")}
      >
        <i className={`fas fa-bars ${status}`}></i>
        <i className={`fas fa-times ${status}`}></i>
      </div>
      <nav className={`navigation ${status}`}>
        <ul>
          {nav.map((element) => (
            <li key={element.id} onClick={() => setStatus("close")}>
              <NavLink to={element.path}>{element.name}</NavLink>
            </li>
          ))}
        </ul>
      </nav>
    </div>
  );
};

export default Navigation;
