import React, { Component } from "react";
import "../scss/LoginPage.scss";
import { Redirect } from "react-router-dom";

const users = [
  {
    id: 1,
    name: "Boya Żeleńskiego Tarnów",
    login: "pwiater",
    password: "techmes1",
    station: "Stacja pogodowa Techmes",
    city: "Tarnów",
    location: "ul. Boya Żeleńskiego 4",
  },
];

class LoginPage extends Component {
  state = {
    data: {
      email: "",
      password: "",
    },
    loading: this.props.loggedIn,
    errors: {},
  };

  handleChange = (e) => {
    this.setState({
      data: { ...this.state.data, [e.target.name]: e.target.value },
    });
  };

  handleSubmit = (e) => {
    e.preventDefault();
    users.forEach((element) => {
      if (
        element.login === this.state.data.email &&
        element.password === this.state.data.password
      ) {
        this.props.handleLogin(element);
      }
    });
  };

  render() {
    const { data } = this.state;
    if (this.state.loading) {
      return <Redirect to="/home" />;
    } else if (!this.state.loading) {
      return (
        <div className="loginPageWrapper">
          <header className="mainLogo">
            <h1>Techmes</h1>
            <p className="app">Zarządzanie stacją pogodową</p>
          </header>
          <div className="loginForm">
            <form onSubmit={this.handleSubmit}>
              <input
                name="email"
                placeholder="login"
                onChange={this.handleChange}
                value={data.email}
              />
              <input
                type="password"
                name="password"
                placeholder="hasło"
                required
                value={data.password}
                onChange={this.handleChange}
              />
              <button className="loginButton">Zaloguj</button>
            </form>
          </div>
        </div>
      );
    }
  }
}

export default LoginPage;
